
import {GenresSeries} from '../Dados/GenresSeries';
import {GenresFilmes} from '../Dados/GenresFilmes';
import {BibliotecaSerie} from '../Dados/BibliotecaSerie';
import {BibliotecaFilmes} from '../Dados/BibliotecaFilmes';

const Tmdb = ()=>{
      let timeSetInterval = 0
      timeSetInterval = JSON.parse(localStorage.getItem('timeSetInterval'))
      if(timeSetInterval<BibliotecaSerie.length){

      const chamadas =  setInterval( ()=>{
        timeSetInterval = timeSetInterval + 100;
        console.log({timeSetInterval})
        let a1 = timeSetInterval
        let b1 = a1 + timeSetInterval
        TmdbBusca([BibliotecaSerie.reverse().slice(a1,b1),GenresSeries,'tv']);
        localStorage.setItem("timeSetInterval",JSON.stringify(timeSetInterval))
        if(timeSetInterval>BibliotecaSerie.length){
          clearInterval(chamadas)
          console.log('tv: ',JSON.parse(localStorage.getItem("Arrytv")))
        }
      },1000)
    }
      let timeSetIntervalMovie = 0
      timeSetIntervalMovie = JSON.parse(localStorage.getItem('timeSetIntervalMovie'))
      if(timeSetIntervalMovie<BibliotecaFilmes.length){
          
      const chamadas02 =setInterval( ()=>{ 
        timeSetIntervalMovie = timeSetIntervalMovie + 100;
        console.log({timeSetIntervalMovie})
        let a2 = timeSetIntervalMovie
        let b2 = a2 + timeSetIntervalMovie
        TmdbBusca([BibliotecaFilmes.reverse().slice(a2,b2),GenresFilmes,'movie']); 
        localStorage.setItem("timeSetIntervalMovie",JSON.stringify(timeSetIntervalMovie))
        if(timeSetIntervalMovie>BibliotecaFilmes.length){
          clearInterval(chamadas02)
          console.log('movie: ',JSON.parse(localStorage.getItem("Arrymovie")))
        }
      },1000)
    } 
     
    const TmdbBusca = async (props)=>
    { let Biblioteca = props[0]    
      let Genre = props[1]
      let tipo = props[2]
      //-----------   
      let buscarlocal = JSON.parse(localStorage.getItem("Arry"+tipo))
      let contar = 0
      for(let i in buscarlocal)
      {contar = contar+buscarlocal[i].length}
      console.log({contar})
          let busca = await BuscaApi({Biblioteca, Genre, tipo, buscarlocal})
          console.log({busca})   
    }


const BuscaApi = async ({Biblioteca, Genre,tipo,buscarlocal}) => {        
    const API_KEY = "5417af578f487448df0d4932bc0cc1a5";
    const API_BASE = 'https://api.themoviedb.org/3/';  
    if(!buscarlocal){
        buscarlocal =[]
    }
   
    const bucarSeasons = async (props)=>{
        let infEp = await props.infEp
        let tipo = await props.tipo
        let b = []
        for(let i = 1 ; i <= infEp.number_of_seasons; i++){
            await fetch(`${API_BASE}${tipo}/${infEp.id}/season/${i}?api_key=${API_KEY}&language=pt-BR`)
            .then((response)=>{return  response.json()})
            .then((data)=>{b.push(data.episodes.length)})   
            .catch((error)=>{console.log(error.message, tipo, 'season')})
        }
        return (await b)
    }
    const bucarId = async (props)=>{
        //busca por ID
        let id = props.id
        let tipo =  props.tipo
        let infEp = await fetch(`${API_BASE}${tipo}/${id}?api_key=${API_KEY}&language=pt-BR`)
        .then((response)=>{return response.json()})
        .then((data)=>{;return  data})   
        .catch(()=>{return null}) 

        //busca por Epsodes
        let seasons = ''
        if(tipo==='tv')
        {seasons = bucarSeasons({infEp,tipo})}
        else{seasons = [0]}        
        return({infEp,seasons})
    } 
    const mountArrySeries = async (props) =>{
        //Montar Arry
        
        let info = await props.infEp
        let ArrySeason = await props.seasons
        let genero = ''
        if(info.genres && info.genres[0]){genero = info.genres[0].name}
        else if(info.genres && info.genres[1]){genero = info.genres[1].name}
        else if(info.genres && info.genres[2]){genero = info.genres[2].name}       
        
        return{                    
            genre: genero,
            id: info.id,
            data : info.first_air_date,
            rating: info.vote_average,
            overview: info.overview,
            backdrop: info.backdrop_path,                
            nome: info.name,
            url: info.id,
            poster: info.poster_path,
            numberEpisodes: ArrySeason,
        };    
    }
    const mountArryFilmes = (props) =>{
        //Montar Arry
        let info = props.infEp
        let ArrySeason = props.seasons
        let genero = ''
        if(info.genres && info.genres[0]){genero = info.genres[0].name}
        else if(info.genres && info.genres[1]){genero = info.genres[1].name}
        else if(info.genres && info.genres[2]){genero = info.genres[2].name}     
        
        return{                    
            genre: genero,
            id: info.imdb_id,
            data : info.release_date,
            rating: info.vote_average,
            overview: info.overview,
            backdrop: info.backdrop_path,                
            nome: info.title,
            url: info.imdb_id,
            poster: info.poster_path,
            numberEpisodes: ArrySeason,
        };    
    }
    let ArryMountAPI = []
    for(let i = 0; i<Biblioteca.length;i++){
        let id = Biblioteca[i].url        
        let busca = await bucarId({id,tipo})
        if(!busca){continue}
        let montarArry = []
        if(tipo==='tv')
        {montarArry = await mountArrySeries(busca)}
        else if(tipo==='movie')
        {montarArry = await mountArryFilmes(busca)}
        ArryMountAPI.push(montarArry);
    }
    
    let NovoArryBuscado = await Promise.all(ArryMountAPI) 
    let antigoArryQuebrado = []  
    
    for(let w in buscarlocal){
        for(let q in buscarlocal[w]){
            antigoArryQuebrado.push(buscarlocal[w][q])
        }
    }
    
    let concatenarArry = [].concat(NovoArryBuscado,antigoArryQuebrado)

    //--Remover Duplicado
    let removerDuplicado = new Set();
    concatenarArry.forEach((x)=>{removerDuplicado.add(x)})
    let removerDuplicado02 = [...removerDuplicado.values()]
    //--
    let filtro01 = Genre.map(x=>{return(
        removerDuplicado02
        .filter(y => y.genre === x.name)
        )})
    let filtro02 = []
    for(let h in filtro01){
            filtro02.push( 
                filtro01[h]
                .filter(y => y.rating > 6)
                .filter(y => y.genre !== '')
                .filter(y => y.backdrop !== '')
                ) 
    }
    let Resultado = []
    for(let j in filtro02){
       if(filtro02[j].length>4){
        Resultado.push(filtro02[j].slice(0,90))
        }
    }
   
    //finalizando
    if(Resultado.length>0){
        localStorage.setItem("Arry"+tipo, await JSON.stringify(Resultado))
        return (await Resultado)
    }
    else{
        return null
    }
}    
}
export default Tmdb;