            "genre": "Animação",
            "id": 24835,
            "data": "2007-10-05",
            "rating": 8.4,
            "overview": "A história de Clannad gira em torno de Tomoya Okazaki, um estudante do terceiro ano do ensino médio que não gosta sua vida. A mãe de Tomoya, Atsuko, morreu quando Tomoya era pequeno, deixando seu pai, Naoyuki, para cuidá-lo. Após o acidente, o pai de Tomoya se tornou viciado em álcool e jogo, e criou brigas com seu filho. Um dia, Naoyuki, novamente discutindo com seu filho, empurrou Tomoya contra a parede, deslocando o ombro de seu filho gravemente. Desde então, Tomoya e seu pai passaram a se tratar bem, mas de uma forma distante, como se eles fossem estranhos ao invés de uma família.  Isso dói mais em Tomoya do que as antigas brigas com o pai, e o ressentimento de voltar para casa leva Tomoya a ficar constantemente fora de casa a noite toda. Além disso,a lesão no ombro incapacitou Tomoya de participar do seu clube de basquete, e o estimulou a se distanciar de sua escola e de outras atividades. Assim, sua vida de delinquente se inicia. O melhor amigo de Tomoya, Youhei Sunohara, que foi expulso do clube de futebol por uma briga, também é um delinquente e,muitas vezes fica em seu dormitório com Tomoya sem fazer nada.  A história começa na segunda-feira 14 de abril de 2003, no início do ano letivo, quando Tomoya encontra por acaso Nagisa Furukawa, uma garota de fala suave um ano mais velha que ele, que está repetindo o ano escolar por ter ficado muito doente no ano anterior. Seu objetivo é participar do clube de teatro para o qual ela não pode entrar devido à sua doença, mas eles descobrem que o clube de teatro foi fechado depois que os membros remanescentes se formaram. Como Tomoya tem muito tempo para livre, ele decide ajudar Nagisa a retomar o clube de teatro. Durante esse tempo, Tomoya faz amizade com várias outras garotas e começa a conhece-las mais profundamente, ajudando-as com seus problemas individuais.",
