            "data": "2016-04-03",
            "rating": 7.4,
            "overview": "A História de Deus\" é a exploração épica de Morgan Freeman e uma reflexão íntima sobre Deus. Cada episódio centrar-se-á numa grande questão sobre o divino: desde o mistério da Criação, ao verdadeiro poder dos milagres até à promessa da ressurreição. Estes mistérios universais têm impacto em toda a gente de todas as fés. Morgan mergulhará nas experiências religiosas e rituais de todo o mundo, do Muro das Lamentações em Jerusalém até à Árvore Bodhi na Índia até às mega igrejas dos Estados Unidos. Ele tornar-se-á numa cobaia em laboratórios científicos para examinar com é que as fronteiras da neurociência e cosmologia estão a intersectar o domínio tradicional da religião. E viajará com arquelogistas para descobrir religiões há muito perdidas dos nossos ancestrais, como nas ruínas de 10.000 anos de Gobekli Tepe, na Turquia. A relação do Homem com Deus é a questão mais importante da história da humanidade. Esta expedição ambiciosa pelas eras e pelo globo procura entender a forma como a fé moldou as nossas vidas, não importando a religião ou crença.",
