            "overview": "A história real de Freddy e Walter - dois jovens judeus eslovacos, que foram deportados para Auschwitz em 1942. Em 10 de abril de 1944, após um planejamento meticuloso e com a ajuda e a resiliência de seus colegas internos, eles conseguiram escapar. Os dois homens são movidos pela esperança de que suas evidências possam salvar vidas. Feridos, eles voltam pelas montanhas para a Eslováquia. Com a ajuda de estranhos pelo caminho, eles finalmente conseguem cruzar a fronteira e encontrar a resistência e a Cruz Vermelha. Eles compilam um relatório detalhado sobre o genocídio sistemático no campo. No entanto, com a propaganda nazista e ligações internacionais ainda em vigor, seu relato parece ser muito angustiante para acreditar.",
